import axios from 'axios'
import nprogress from 'nprogress'
import 'nprogress/nprogress.css'
import qs from 'qs'

const requests = axios.create({
  baseURL: 'http://leiyu.icu:5678/api',
  //请求超时时间
  timeout: 5000,
})

//请求拦截器requests.interceptors.request
requests.interceptors.request.use((config) => {
  if (localStorage.token) {
    config.headers.Authorization = localStorage.token
  }
  nprogress.start()
  //config：配置对象，对象里有请求头header属性
  return config
})

//响应拦截器
requests.interceptors.response.use(
  (res) => {
    nprogress.done()
    return res.data
  },
  (err) => {
    //终止promis链
    return Promise.reject(new Error('faile'))
  }
)

//请求搜索结果
export const reqResultList = (keyword, reqPage = 1) => {
  const toltalSearch = {
    query: keyword,
    page: reqPage,
    limit: 10,
    order: 'desc',
  }
  const data = JSON.stringify(toltalSearch)

  return requests({ url: '/query', method: 'post', data: data })
}
//用户登录
export const userLogin = (usn, psw) => {
  const data = qs.stringify({
    username: usn,
    password: psw,
  })

  return requests({ url: '/login', method: 'post', headers: { 'content-type': 'application/x-www-form-urlencoded' }, data: data })
}
//发送注册信息
export const userRegister = (usn, psw) => {
  const data = qs.stringify({
    username: usn,
    password: psw,
  })

  return requests({ url: '/register', method: 'post', headers: { 'content-type': 'application/x-www-form-urlencoded' }, data: data })
}
//请求用户名
export const getUsername = (token) => {
  return requests({ url: '/favorite/username', method: 'get' })
}

//新建收藏夹
export const addNewFvr = (name) => {
  const data = qs.stringify({
    favorite_name: name,
  })
  return requests({ url: '/favorite/add', method: 'post', headers: { 'content-type': 'application/x-www-form-urlencoded' }, data: data })
}
