import Vue from 'vue'
import Vuex from 'vuex'
import { reqResultList } from '@/api/index'
import { getUsername } from '@/api/index'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    //搜索关键词
    keyword: '',
    //搜索的结果
    documents: [],
    limit: 0,
    page: 0,
    total: 0,
    pageCount: 0,
    time: 0,
    //是否登录
    isLogin: false,
    //登录的用户名
    loginedUsername: '',
  },
  getters: {},
  mutations: {
    //设置搜索关键词
    SETINPUT(state, value) {
      state.keyword = value
    },
    //设置请求回来的搜索结果
    GETRESULT(state, value) {
      state.documents = value.documents
      state.limit = value.limit
      state.page = value.page
      state.total = value.total
      state.pageCount = value.pageCount
      state.time = value.time
    },
    //读取登录用户的用户名
    AUTOLOGIN(state, value) {
      state.loginedUsername = value
    },
    //是否显示用户模块
    SHOWUSERELEMENT(state, value) {
      state.isLogin = value
    },
    //用户退出登录
    LOGOUT(state, value) {
      state.isLogin = false
      state.loginedUsername = ''
    },
  },
  actions: {
    //获取特定页数的搜索结果
    async getResult(content, value) {
      const res = await reqResultList(value)
      if ((res.state = 'true')) {
        content.commit('GETRESULT', res.data)
      }
    },
    //搜索结果页数跳转
    async pageJump(content, value) {
      const res = await reqResultList(value.input, value.targetPage)
      if ((res.state = 'true')) {
        content.commit('GETRESULT', res.data)
      }
    },
    //自动登录检测
    async autoLogin(content, token) {
      const res = await getUsername(token)

      if (res.state === true) {
        content.commit('AUTOLOGIN', res.data.username)
        content.commit('SHOWUSERELEMENT', true)
      } else {
        content.commit('SHOWUSERELEMENT', false)
      }
    },
  },
  modules: {},
})
