<template>
  <div class="user-container">
    <div class="logined" v-if="isLogin">
      <div @click="goUserInfo" class="userinfo">
        <div class="userimg"><img src="@/assets/useriomg.png" alt=""></div>
        <span class="username">{{loginedUsername}}</span>
      </div>
      <a href="javascript:;" @click="logout" style="vertical-align: middle">注销</a>

    </div>

    <div class="unlogin" v-else>
      <div class="login">
        <el-button type="primary" @click="goLogin">登录</el-button>
      </div>
      <div class="register">
        <router-link to="/register">注册</router-link>
      </div>
    </div>

  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  computed: {
    ...mapState(['isLogin', 'loginedUsername'])
  },
  methods: {
    goLogin() {
      this.$router.push('/login')
    },
    goUserInfo() {
      this.$router.push('/userinfo')
    },
    logout() {
      localStorage.removeItem('token')
      this.$store.commit('LOGOUT')
    }
  },
  mounted() {
    if (localStorage.getItem('token')) {
      this.$store.dispatch('autoLogin', localStorage.getItem('token'))
    }
  }
}
</script>

<style lang="less" scoped>
.user-container {
  position: absolute;
  top: 0;
  right: 0;
  padding-top: 20px;
  .logined {
    height: 30px;
    line-height: 30px;
    padding-right: 20px;
    .userinfo {
      display: inline-block;
      &:hover {
        cursor: pointer;
        .username {
          color: #409eff;
        }
      }
      .userimg {
        display: inline-block;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        overflow: hidden;
        vertical-align: middle;
        margin-right: 10px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .username {
        vertical-align: middle;
        padding-right: 10px;
      }
    }
  }
  .unlogin {
    .login {
      display: inline-block;
      padding-right: 20px;
    }
    .register {
      display: inline-block;
      padding-right: 30px;
    }
  }
}
</style>