<template>
  <div class="result-container">
    <!-- 搜索框 -->
    <SearchInput></SearchInput>
    <!-- 用户模块 -->
    <User></User>
    <!-- 搜索结果 -->
    <div class="res-content">
      <div class="resInfo">共找到 {{total}} 条结果</div>
      <Tip v-for="item in documents" :key="item.id" :imgsrc="item.document.Url" :textcontent="item.text" :fvrsList="fvrsList" :tipId="item.id"></Tip>
    </div>
    <!-- 页码 -->
    <div class="pagination">
      <el-pagination background layout="prev, pager, next" :total="total" :current-page="page" @current-change="currentChange" @prev-click="prevClick" @next-click="nextClick" ref="pagination">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import User from '@/components/user/index.vue'
import Tip from '@/views/Result/Tip'
import { mapState } from 'vuex'
import { getFvrsList } from '@/api'

export default {
  data() {
    return {
      fvrsList: []
    }
  },
  methods: {
    currentChange() {
      const targetPage = this.$refs.pagination.internalCurrentPage
      this.$store.dispatch('pageJump', { targetPage, input: this.keyword })
    },
    prevClick() {
      const targetPage = this.page - 1
      this.$store.dispatch('pageJump', { targetPage, input: this.keyword })
    },
    nextClick() {
      const targetPage = this.page + 1
      this.$store.dispatch('pageJump', { targetPage, input: this.keyword })
    },
    //获取收藏夹列表
    async loadFvrList() {
      const res = await getFvrsList()
      console.log(1)
      if (res.state === true) {
        this.fvrsList = res.data.favorite_list
      }
    }
  },

  computed: {
    ...mapState(['documents', 'pageCount', 'total', 'page', 'keyword', 'currentPage'])
  },
  watch: {},
  mounted() {
    this.loadFvrList()
    // this.keyword = this.$route.query.input
  },

  components: {
    Tip,
    User
  }
}
</script>

<style lang="less" scoped>
.result-container {
  padding-top: 20px;
  .res-content {
    width: 800px;
    margin: 0 auto;
    margin-top: 20px;
    .resInfo {
      margin-bottom: 20px;
    }
  }
  .pagination {
    text-align: center;
  }
}
</style>