<template>
  <div class="userinfo-container">
    <!-- 新建收藏夹 -->
    <el-form :model="addFvr" class="demo-dynamic" ref="addFvrs">
      <el-form-item label="新建收藏夹:">
        <el-input v-model="addFvr.favorite_name"></el-input>
        <el-button @click="addFvrSubmit" type="primary">新建</el-button>
      </el-form-item>
    </el-form>
    <!-- 用户的收藏夹列表 -->
    <div class="fvrList">
      <el-collapse v-model="activeNames">
        <el-collapse-item v-for="(item,index)  in fvrsList" :key="item" class="fvrTip" :name="index" @mouseenter="showDel">
          <template slot="title">
            {{item}} <a href="javascript:;" class="delBtn" ref="delBtn" @click="delFvrList(item)">删除</a>
          </template>

          <Tip v-for="t in fvrsListItems[index]" :key="t.id" :imgsrc="t.document.Url" :textcontent="t.text" :tipId="t.id" :fvrName="item" @delItemSuccess="reloadList
          "></Tip>
        </el-collapse-item>
      </el-collapse>
    </div>

  </div>
</template>

<script>
import { addNewFvr, delFvr, getFvrsList, getFvrInnerItem } from '@/api'
import Tip from '@/views/Result/Tip'

export default {
  data() {
    return {
      addFvr: {
        favorite_name: ''
      },
      fvrsList: [],
      fvrsListItems: [],
      activeNames: ['1'],
      visible: false
    }
  },
  methods: {
    //显示删除收藏夹的按钮
    showDel() {
      console.log(1)
      this.$refs.delBtn.style.display = 'inline-block'
    },
    //删除收藏tip后重写加载收藏夹
    reloadList() {
      this.fvrsListItems = []
      this.loadFvrList()
    },
    //新建收藏夹
    async addFvrSubmit() {
      const res = await addNewFvr(this.addFvr.favorite_name)
      console.log(res)
      if (res.state === true) {
        this.loadFvrList()
      }
    },
    // //请求各个收藏夹的详细内容
    // async loadFvrInnerItem(fvrname) {
    //   const res = await getFvrInnerItem(fvrname)
    //   if (res.state === true) {
    //     console.log(res)
    //   }
    // },
    //获取收藏夹列表
    async loadFvrList() {
      const res = await getFvrsList()
      if (res.state === true) {
        this.fvrsList = res.data.favorite_list
        for (let index = 0; index < this.fvrsList.length; index++) {
          const res = await getFvrInnerItem(this.fvrsList[index])
          this.fvrsListItems.push(res.data)
        }
      }
    },
    //删除收藏夹
    async delFvrList(fvrname) {
      const res = await delFvr(fvrname)
      if (res.state === true) {
        this.loadFvrList()
      }
    }
  },
  mounted() {
    this.loadFvrList()
  },
  components: { Tip }
}
</script>

<style lang="less" scoped>
.el-collapse-item__header {
  font-size: 22px;
}

.userinfo-container {
  box-sizing: border-box;
  width: 900px;
  margin: 0 auto;
  margin-top: 100px;
  padding: 50px;
  border: 1px solid #ccc;
  border-radius: 10px;
  /deep/.el-form-item__label {
    font-size: 16px;
  }
  .el-input {
    width: 200px;
    margin-right: 10px;
  }

  .delBtn {
    display: none;
    margin-left: 10px;
    font-size: 12px;
    &:hover {
      color: red;
    }
  }

  /deep/.el-collapse-item__header {
    font-size: 22px;
    &:hover .delBtn {
      display: inline-block;
    }
  }
}
</style>