<template>
  <div class="searchInput-container" @keyup.enter="goSearch(input)">

    <el-input placeholder="请输入内容" v-model="input" clearable>
    </el-input>
    <el-button type="primary" @click="goSearch(input)">搜索</el-button>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      input: ''
    }
  },
  methods: {
    goSearch(input) {
      this.$store.commit('SETINPUT', input)
      this.$store.dispatch('getResult', input)

      if (this.$route.path === '/home') {
        this.$router.push({ path: 'result', query: { input: this.keyword } })
      } else {
        this.$router.push({ path: 'result', query: { input: this.keyword } })
      }
    }
  },
  computed: {
    ...mapState({ keyword: state => state.keyword })
  },
  mounted() {
    this.input = this.keyword
  }
}
</script>

<style lang="less" scoped>
.searchInput-container {
  margin: 0 auto;
  width: 570px;
  height: 41px;
  /deep/.el-input {
    display: inline-block;
    width: 500px;
    height: 41px;
    .el-input__inner {
      height: 41px;
      border-right: none;
      border-top-right-radius: 0px;
      border-bottom-right-radius: 0px;
    }
  }
  /deep/ .el-button {
    height: 41px;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
    border: none;
  }
}
</style>