<template>
  <div class="login-container">

    <div class="loginbox">

      <h4>登陆</h4>
      <form action="#">
        <div class="unamebox">
          <div class="uname">用户名：</div><input type="text" placeholder="请输入用户名" v-model="usn">
        </div>
        <div class="pswbox">
          <div class="psw">登陆密码：</div><input type="password" placeholder="请输入密码" v-model="psw">
        </div>
        <button class="loginbtn" @click="goLogin(usn,psw)">登陆</button>
        <router-link to="/register" class="regbtn">注册</router-link>
      </form>

    </div>
  </div>
</template>

<script>
import { userLogin } from '@/api'
export default {
  data() {
    return {
      usn: '',
      psw: ''
    }
  },
  methods: {
    async goLogin(usn, psw) {
      const res = await userLogin(usn, psw)
      if (res.state === true) {
        localStorage.setItem('token', res.data.token)
        this.$router.push('/home')
      } else {
        alert('登录失败！' + res.message)
      }
    }
  }
}
</script>

<style lang="less" scoped>
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.loginbox {
  position: absolute;
  top: 200px;
  left: 600px;
  width: 725px;

  text-align: center;
  background-color: #fff;
  border: 1px solid #409eff;
  border-radius: 15px;
}

h4 {
  font-weight: normal;
  font-size: 32px;
  margin-top: 50px;
}

.unamebox,
.pswbox {
  margin-top: 40px;
  input {
    padding-left: 10px;
    width: 450px;
    height: 40px;
    border: 1px solid #ccc;
    font-size: 18px;
    &:focus-visible {
      outline-color: #409eff;
    }
  }
}

.uname,
.psw {
  display: inline-block;
  height: 21px;
  width: 100px;
  text-align: right;
  font-size: 20px;
}

.loginbtn {
  margin-top: 50px;
  width: 240px;
  height: 60px;
  border: 1px solid #409eff;
  background-color: #fff;
  border-radius: 5px;
  cursor: pointer;
}

.regbtn {
  display: block;
  margin-top: 10px;
  margin-bottom: 30px;
}
</style>