<template>
  <div class="tip-container">
    <div class="star" v-if="$route.path==='/result'">
      <el-popover placement="right" width="400" trigger="click" v-model="visible">
        <div class="fvrList">
          <p class="fvrname" v-for="item in fvrsList" :key="item" @click="addToFvrs(item,tipId)">{{item}}</p>
        </div>
        <el-button slot="reference" ref="starBtn">+</el-button>
      </el-popover>
    </div>
    <div class="clearStar" v-if="$route.path==='/userinfo'" @click="clearTipStart(fvrName,tipId)">

      <el-button slot="reference">X</el-button>

    </div>

    <div class="tip-img"><img :src="imgsrc" alt=""></div>
    <div class="text">{{textcontent}}</div>

  </div>
</template>

<script>
import { addFvrItem, delFvrItem } from '@/api'

export default {
  props: ['imgsrc', 'textcontent', 'fvrsList', 'tipId', 'fvrName'],
  data() {
    return {
      visible: false
    }
  },
  methods: {
    async addToFvrs(fvrName, tipId) {
      const res = await addFvrItem(fvrName, tipId)
      if (res.state === true) {
        alert('添加成功')
        this.visible = false
      }
    },
    async clearTipStart(fvrname, id) {
      const res = await delFvrItem(fvrname, id)
      if (res.state === true) {
        this.$emit('delItemSuccess')
      }
    }
  },
  mounted() {},
  components: {}
}
</script>

<style lang="less" scoped>
p:hover {
  cursor: pointer;
  background-color: #409eff;
}
.tip-container {
  position: relative;
  width: 100%;
  height: 100px;
  margin-bottom: 20px;
  border: 1px solid #f1f2f3;
  border-radius: 8px;
  background-color: #f6f7f8;

  display: flex;
  .star,
  .clearStar {
    position: absolute;
    top: 30px;
    right: 25px;
    font-size: 20px;

    &:hover {
      cursor: pointer;
    }
  }

  .tip-img {
    flex: 2;
    padding: 10px;

    img {
      width: 100%;
      height: 100%;
      border-radius: 8px;
    }
  }
  .text {
    flex: 8;
    padding: 10px;
    width: 0px;
    word-break: break-all;
  }
}
</style>