<template>
  <div class="login-container">

    <div class="loginbox">

      <h4>注册</h4>
      <form action="#">
        <div class="unamebox">
          <div class="uname">用户名：</div><input type="text" placeholder="请输入用户名" v-model="regUsername" @blur="regExp1(regUsername,usnPtn)"><span class="crt" v-if="isUsnCrt"> √</span><span class="icrt" v-else> X</span>
          <p class="iptTips">字母开头，允许5-16字节，允许字母数字下划线</p>
        </div>
        <div class="pswbox">
          <div class="psw">登陆密码：</div><input type="password" placeholder="请输入密码" v-model="regPassword" @blur="regExp2(regPassword,pswPtn)"><span class="crt" v-if="isPswCrt"> √</span><span class="icrt" v-else> X</span>
          <p class="iptTips">以字母开头，长度在6~18之间，只能包含字母、数字和下划线</p>
        </div>
        <div class="pswbox">
          <div class="psw">重复输入密码：</div><input type="password" placeholder="请重复输入密码" v-model="reRegPassword" @blur="regExp3(reRegPassword)"><span class="crt" v-if="isRpswCrt"> √</span><span class="icrt" v-else> X</span>
        </div>
        <button class="regbtn" @click="subReg(regUsername,regPassword)" :disabled="!allCrt">注册</button>

      </form>

    </div>
  </div>
</template>

<script>
import { userRegister } from '@/api/index'
export default {
  data() {
    return {
      //用户输入的注册信息
      regUsername: '',
      regPassword: '',
      reRegPassword: '',
      //字母开头，允许5-16字节，允许字母数字下划线
      usnPtn: /^[a-zA-Z][a-zA-Z0-9_]{4,15}$/,
      //密码(以字母开头，长度在6~18之间，只能包含字母、数字和下划线)
      pswPtn: /^[a-zA-Z]\w{5,17}$/,
      //判断输入的注册信息格式是否正确，所有都微true才能启用提交按钮
      isUsnCrt: false,
      isPswCrt: false,
      isRpswCrt: false
    }
  },
  computed: {
    allCrt() {
      return this.isUsnCrt && this.isPswCrt && this.isRpswCrt
    }
  },
  methods: {
    //用户名的格式判断
    regExp1(str, pattern) {
      if (pattern.test(str)) {
        this.isUsnCrt = true
      } else {
        this.isUsnCrt = false
      }
    },
    //第一次输入密码的格式判断
    regExp2(str, pattern) {
      if (pattern.test(str) && this.reRegPassword === '') {
        this.isPswCrt = true
      } else if (pattern.test(str) && this.reRegPassword !== '' && this.reRegPassword !== this.regPassword) {
        this.isRpswCrt = false
      } else {
        this.isPswCrt = false
      }
    },
    //重复输入密码的格式判断
    regExp3(str) {
      if (str === this.regPassword) {
        this.isRpswCrt = true
      } else {
        this.isRpswCrt = false
      }
    },
    //提交注册请求
    async subReg(usn, psw) {
      const res = await userRegister(usn, psw)
      if (res.state === true) {
        alert('注册成功')
        this.$router.push('/home')
      } else {
        alert('注册失败，' + res.message)
      }
    }
  }
}
</script>

<style lang="less" scoped>
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.iptTips {
  text-align: left;
  padding-left: 200px;
}
.crt {
  color: green;
}
.icrt {
  color: red;
}

.loginbox {
  position: absolute;
  top: 200px;
  left: 600px;
  width: 725px;

  text-align: center;
  background-color: #fff;
  border: 1px solid #409eff;
  border-radius: 15px;
}

h4 {
  font-weight: normal;
  font-size: 32px;
  margin-top: 50px;
}

.unamebox,
.pswbox {
  margin-top: 40px;
  input {
    padding-left: 10px;
    width: 450px;
    height: 40px;
    border: 1px solid #ccc;
    font-size: 18px;
    &:focus-visible {
      outline-color: #409eff;
    }
  }
}

.uname,
.psw {
  display: inline-block;
  height: 21px;
  width: 140px;
  text-align: right;
  font-size: 20px;
}

.regbtn {
  margin: 50px 0 30px 0;
  width: 240px;
  height: 60px;
  border: 1px solid #409eff;
  background-color: #fff;
  border-radius: 5px;
  cursor: pointer;
}
</style>